import { theme } from '@hackclub/design-system'

export const colors = {
  primary: '#ff16D1',
  black: '#1f2d3d',
  steel: '#273444',
  slate: '#3c4858',
  grey: '#8492a6',
  smoke: '#e0e6ed',
  snow: '#f9fafc',
  white: '#ffffff'
}

theme.colors = colors
theme.font = '"Fakt Soft", Roboto, sans-serif'

export default theme



