import React from 'react'
import { injectGlobal, ThemeProvider as Root } from 'styled-components'
import theme from './config'
import Background from './background.jpg'
import font2 from './MFDingDing_Noncommercial-Regular.ttf'

injectGlobal`
  @font-face {
    font-family: "DingDing";
    src: url(${font2});
  }
  body {
    margin-top: 2em;
    font-family: "Fakt Soft", Roboto, sans-serif;
    background-image: url(${Background});
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    min-height: 100vh;
    color: ${theme.colors.black};
    line-height: 1.6;
    positive: relative;
  }
  * {
    box-sizing: border-box;
  }
  a {
    box-shadow: none;
    text-decoration: none;
    color: ${theme.colors.primary};
  }
  ul, ol {
    margin: 0;
    padding: 0;
  }
  img {
    max-width: 100%;
  }
  h1 {
    font-family: 'DingDing', sans-serif;
    padding-top: 1em;
    font-weight: initial !important;
    font-size: 50px !important;
    padding-top : 0;
    letter-spacing: 2px;
  }
  article {
    font-family: "Fakt Soft", Roboto, sans-serif !important;
    border-radius: 8px !important;
    padding: 32px;
    background: white;
    box-shadow: ${theme.boxShadows[1]};
    transition: box-shadow .125s ease-out;
    &:hover {
      box-shadow: ${theme.boxShadows[2]};
    }
  }
  p {
    margin-block-start: 0em;
    margin-block-end: 0em;
  }
  h3 {
    background: linear-gradient(to right, #4791ff 0%, #ff9efa 40%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  #date {
    font-size: 100px !important;
  }
  h2 {
    font-size: 24px !important;
  }
  #proceedButton {
    font-family: 'DingDing', sans-serif;
    background: linear-gradient(to right, #4791ff 0%, #ff9efa 80%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    padding: 0 1em;
    
    width: fit-content;
    color: black;
    margin-bottom: 2em;
    font-size: 2.5em;
    font-weight: normal;
  }
  #test {
    background: white;
    position: absolute;
    display: block;
    border-radius: 50px;
    height: 50px;
    left: 50%;
    transform: translate(-50%, -50%);
  }
`

const ThemeProvider = props => (
  <Root theme={theme} children={<div>{props.children}</div>} />
)

export default ThemeProvider



